import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Calendar from '../views/Calendar.vue'
import Books from '../views/Books.vue'
import ViewBook from '../views/ViewBook.vue'
import Personalities from '../views/Personalities.vue'
import Personality from '../views/Personality.vue'
import ListMgt from '../views/ListMgt.vue'

Vue.use(VueRouter)

const routes = [{
	path: '/',
	name: 'Home',
	component: Home,
	redirect: '/calendar',	
	children: [{
		path: '/books',
		name: 'Books',
		component: Books
	}, {
		path: '/books/:code',
		name: 'ViewBook',
		component: ViewBook
	}, {
		path: '/calendar',
		name: 'Calendar',
		component: Calendar
	}, {
		path: 'vaishnava/:category',
		name: 'Personalities',
		component: Personalities,
		children: [{
			path: ':code',
			name: 'Personality',
			component: Personality
		}]
	}]
}, {
	path: '/listmgt',
	name: 'ListMgt',
	component: ListMgt
}]

const router = new VueRouter({
	routes
})

export default router