	link: 'http://scsmathinternational.com/SrilaBNAcharyaMaharaj.php',
	desc: `
		<p><b>Om Visnupad Srila Bhakti Nirmal Acharya Maharaj</b> is the Successor President Sevaite Acharya of Sri Chaitanya Saraswat Math, publicly appointed by <b>Srila Bhakti Sundar Govinda Dev-Goswami Maharaj</b> in his declaration to the grand international assembly at Sri Chaitanya Saraswat Math on his Sri Vyasa-Puja Day, 4 December 2009.</p>
		<p>Srila Acharya Maharaj entered the gates of Sri Chaitanya Saraswat Math in <b>1992</b>, and despite being both a qualified lawyer and teacher, he immediately cut off all connection with outside obligations and laid his head with firm faith and conviction at the lotus feet of Srila Govinda Maharaj with an intensity which has never wavered.</p>
		<p>From the very beginning of his joining the Math, Srila Acharya Maharaj served with great sincerity, enthusiasm and energy, and within only two years Srila Govinda Maharaj began personally entrusting him with greater responsibilities and a managing role within the Math.</p>
		<p><b>In 1999</b>, at the repeated request of His Divine Grace Srila Govinda Maharaj, Srila Acharya Maharaj accepted the sannyas order of life</b>. From that early time Srila Govinda Maharaj began to express his consideration of Srila Acharya Maharaj as the most suitable candidate for the future post of Successor Sevaite President Acharya of Sri Chaitanya Saraswat Math.</p>
		<p>Over the years Srila Acharya Maharaj served Srila Govinda Maharaj intensely and faithfully in all aspects of running the Temple. Srila Govinda Maharaj engaged him to assist with the construction of the four storey <b>'Seva-Kunja' Centenary Memorial Building</b> of <b>Srila B.R. Sridhar Dev-Goswami Maharaj</b>, opened in 1995.</p>
		<p>Being pleased by seeing his capacity and mood, Srila Govinda Maharaj entrusted Srila Acharya Maharaj with all the construction and development that has taken place at the Math since then. These projects include Sri Govinda Kunda and the Mandir in the centre of the Kunda, thus fulfilling a cherished desire of both Srila Sridhar Maharaj and Srila Govinda Maharaj; the three storey Seva Ashram beside the Kunda, and the adjacent five-storey Sevak Bhavan guesthouse with over sixty rooms for pilgrims and guests.</p>
		<p>Srila Acharya Maharaj built the beautiful Temple and Ashram at Sripat Bamunpara, and he greatly pleased Srila Gurudev Srila Govinda Maharaj by making our large Math in Ekachakra, the Appearance Place of Sri Nityananda Prabhu. Srila Govinda Maharaj expressed that by this, Srila Gurudev was able to serve Sri Nityananda Prabhu during his last days before disappearing from this world.</p>
		<p>In addition Srila Acharya Maharaj supervises several smaller projects and centres associated with Sri Chaitanya Sarawat Math, while himself constantly engaged in practical, broad preaching. He regularly leads village to village preaching within West Bengal, gives frequent public lectures, and through these enthusiastic activities many thousands of pilgrims now join Sri Chaitanya Saraswat Maths major annual festivals.</p>
		<p>Srila Acharya Maharaj has also continued and developed the annual Kartik Pilgrimage to Govardhan and Vrindavan, and the annual Sri Puri Rathayatra Pilgrimage which are attended by devotees from home and abroad.</p>
		<p>Srila Acharya Maharaj has also extended his preaching internationally. Srila Govinda Maharaj took Srila Acharya Maharaj with him on a number of World Tours, and in later years, when Srila Govinda Maharaj was physically not well enough to go himself, several times he sent Srila Acharya Maharaj on his behalf, considering that his dearmost disciple could do what he could no longer do.</p>
	`,
