  code: 'AshramMhj',
	desc: `
		<p><b>Srila Bhakti Kusum Ashram Maharaj</b> joined ISKCON in February 1974 in Mexico City at the age of 23. He received his first and second initiations a year later from Srila Bhaktivedanta Swami during His visit to Mexico. Then Brajendra Kumar Prabhu joined the Mission of Srila Sridhar Maharaj in 1982 and in March of the same year he took sannyasa from His Divine Grace at Sri Chaitanya Saraswat Math, Navadwip.</p>
		<p>Srila Ashram Maharaj is currently the head of the Sri Chaitanya Saraswat Math centers in Mexico and Latin America. He is in charge of eleven ashrams in Mexico and hundreds of followers in Latin America.</p>
		<p>Being one of the closest associates of Srila Gurudeva, Srila Ashram Maharaj spends all his free time at the lotus feet of Srila Govinda Maharaj as his close friend and advisor. Srila Ashram Maharaj is the editor-in-chief of many Mission publications and Spanish translations. On his piece of land in Mexico, he builds a large Temple and ashram complex. His Lordship is an example of self-sacrifice, unceasing service in all circumstances and a deep loving heart - a source of inspiration for countless souls around the world.</p>
	`
