  name: 'Srila Bhakti Vijay Trivikram Maharaj',
  shortName: 'Srila B.V. Trivikram Maharaj',
  code: 'TrivikramMhj',
	desc: `
		<p><b>Srila Bhakti Vijay Trivikrama Maharaj</b> is the Acharya of Sri Chaitanya Saraswat Math in Brazil,  nominated to this post by Srila Bhakti Sundar Govinda Dev-Goswami Maharaj.</p>
		<p>He appeared in August 12, 1964 in Sao Paulo, Brazil.</p>
		<p>In 1994 His Divine Grace Srila Bhakti Sundar Govinda Dev-Goswami Maharaj, at the invitation of Sripad Tapasvi Mahraj, came to Brazil with his disciples and sannyasis. Sripad Tapasvi Maharaj  organized a beautiful festival in Vargem Grande - Sao Paulo, and at that time  Srila Trivikrama Maharaj met His Divine Grace Srila Govinda Maharaj for the first time.</p>
		<p>In 1998, during the second visit of Srila Govinda Maharaj to Brazil, Srila Trivikrama Maharaj received his  first initiation and the name Ananga Krisna Das. During his visit to Brazil, Srila Govinda Maharaj also established the deity of Sriman Mahaprabhu  Sri Gauranga Sundar   in the Sri Chaitanya Sridhar Govinda Seva Ashram.</p>
		<p>In 2000 Srila Trivikrama Maharaj became a personal bodyguard of  His Divine Grace. Due to this he got an opportunity to receive many personal instructions, as well as the great affection of Srila Gurudeva. On several occasions, His Divine Grace expressed his affection for Srila Trivikrama Maharaj and high appreciation for his service. In 2003 Srila Govinda Maharaj initiated Srila Trivikrama Maharaj as a sannyasi (monk) and asked Brazilian devotees  to follow Srila Trivikrama Maharaj as the leader of the Brazilian mission.</p>
		<p>In 2010 Srila Govinda Maharaj disappeared from this world. Prior to his departure he appointed Srila Trivikrama Maharaj as the Acharya of Brazil. Since then, Srila Trivikrama Maharaj has been traveling throughout Brazil to share the teachings of his masters in line of Sri Chaitanya Saraswat Math.
	`
