  name: 'Srila Bhakti Sudhir Goswami Maharaj',
  shortName: 'Srila B.S. Goswami Maharaj',
  code: 'GoswamiMhj',
	desc: `
		<p><b>Srila Bhakti Sudhir Goswami Maharaj</b> is a theologian, writer-publicist, itinerant monk, one of the brightest representatives of the Vaishnav tradition in Hinduism.</p>
		<p>Srila Goswami Maharaj appeared in 1952. His spiritual quest came just in the turbulent 60s, when progressive-minded youth rushed in search of another reality, which lies on the other side of gross matter, exploitation and lies. People felt an acute lack of freedom, love, beauty, harmony, which would not be transitory moments, but permeate the entire existence.</p>
		<p>Meetings with Indian gurus Bhaktivedanta Swami Prabhupada and Srila Sridhar Maharaj , who revealed to Westerners the meaning of the sacred texts of ancient India, awakened Srila Goswami Maharaj's interest in the spiritual heritage of Indian sages.</p>
		<p>Based on the conversations of the guru with his disciples, Goswami Maharaj compiled several books that are considered the quintessence of Vaishnavist philosophy: The Search for Sri Krishna, the Beautiful Reality , Sri Guru and His Grace , The Golden Volcano of Divine Love , The Love Search for the Lost Servant and Subjective Evolution consciousness ".</p>
		<p>In the late 1970s, Goswami Maharj traveled to Eastern European countries, meeting and talking with people at universities. Then, in 1978, he first visited Russia.</p>
		<p>Srila Goswami Maharaj is currently traveling the world spreading and making more accessible the legacy of Srila Sridhar Maharaj and Srila Govinda Maharaj . In his will, Srila Govinda Maharaj expressed his desire for Srila Goswami Maharaj to join the council of acaryas of Sri Chaitanya Saraswat Math (along with Sripad Janardan Maharaj, Srila Ashram Maharaj, Sripad Avadhut Maharaj and Prabad B. Mahan Rahn Rahn). He is one of the interesting thinking theologians of our time. On the subject of his spiritual search, Goswami Maharaj says: "I am not interested in religion, I am interested in Reality." And this characterizes him not just as a religious figure, but as a spiritual researcher.</p>
	`
