<template>
	<div class="column layout-margin8 y-center">
		<div class="row w-70">Server Folder URL</div>
		<input v-model="newCountry.urlServer" class="w-70">
		<div class="row w-70">Client Folder URL</div>
		<input v-model="newCountry.urlClient" class="w-70">
		<div class="row w-70">Gaurabda</div>
		<input v-model="newCountry.gaurabda" class="w-70">
		<button @click="log()" class="w-70">Write</button>
	</div>
</template>
<script>
import { BASEURL } from '../constants';
var cities = require('../assets/js/cities');

export default {
	name: 'ListMgt',
	data() {
		return {
			newCountry: {
				urlServer: '/home/suyash/Documents/Projects/SCSMath/scsmath-rails/app/assets',
				urlClient: '/home/suyash/Documents/Projects/SCSMath/front/src/assets/cal-data',
				gaurabda: 536
			}
		}
	},
	methods: {
		log() {
			var vm = this;
			for (let i = 0; i < cities.length; i++) {
				vm.$http.post(BASEURL + 'ctrl/city/' + i, vm.newCountry);
			}
		}
	},
}
</script>
