import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentMonth: {
      weeks: [],
      day1Index: 0,
      dayLastIndex: 0,
      index: 0,
      year: 0
    },
    months: [{ name: 'January', days: 31 }, { name: 'February', days: 28 }, { name: 'March', days: 31 }, { name: 'April', days: 30 }, { name: 'May', days: 31 }, { name: 'June', days: 30 }, { name: 'July', days: 31 }, { name: 'August', days: 31 }, { name: 'September', days: 30 }, { name: 'October', days: 31 }, { name: 'November', days: 30 }, { name: 'December', days: 31 }],
    days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
    today: {},
    selectedLocation: { name: 'Select Location...' },
    calDates: {},
    month: [],
    isDateSelected: true,
    selectedDate: {},
    AppDate: {
      init(date) {
        let i;
        const keys = Object.keys(date);
        for (i = 0; i < keys.length; i++) {
          const key = keys[i];
          this[key] = date[key];
        }
        this.dateText = this.getDateText();
      },
      getDateText() {
        return this.year + '-' + ('0' + (this.month + 1)).slice(-2) + '-' + ('0' + this.date).slice(-2);
      }
    }
  },
  mutations: {
    setMonth(state, month) {
      state.month = month;
    },
    setMonthVal(state, { key, val }) {
      state.currentMonth[key] = val;
    },
    setToday(state) {
      const today = new Date();
      let todayObj = Object.create(state.AppDate);
      todayObj.init({
        date: today.getDate(),
        day: today.getDay(),
        month: today.getMonth(),
        year: today.getFullYear() 
      });
      state.today = todayObj;
    },
    setSelectedLocation(state, location) {
      state.selectedLocation = location;
      localStorage.location = location['city_id'];
    },
    setCalDates(state, locationImport) {
      for (let j in locationImport) {
        addDetails(locationImport[j], j);
        for (let k = 0; k < locationImport[j].events.length; k++) {
          if (locationImport[j].events[k].img) {
            if (!locationImport[j].img) {
              locationImport[j].img = locationImport[j].events[k].img;
            }
          }
        }
      }
      state.calDates[state.selectedLocation['city_id']] = locationImport;

			function addDetails(date, dateText) {
				date.dateText = dateText;
				let dateTextSplit = dateText.split('-');
				date.year = parseInt(dateTextSplit[0]);
				date.month = parseInt(dateTextSplit[1]) - 1;
				date.date = parseInt(dateTextSplit[2]);
				return date;
			}
    },
    setIsDateSelected(state, isDateSelected) {
      state.isDateSelected = isDateSelected;
    },
    checkFeb(state) {
			if (state.currentMonth.index === 1 && (state.currentMonth.year % 4) === 0) {
				state.months[1].days = 29;
			} else {
				state.months[1].days = 28;
			}
		},
  },
  actions: {
    getMonth({ commit, state }, { date, dateIndex }) {
			let numOfDays = state.months[state.currentMonth.index].days,
				month = [],
				appDate;
			const AppDate = state.AppDate;
			state.currentMonth.day1Index = date === 1 ? dateIndex : ((dateIndex - (date % 7)) + 1 + 7) % 7;

			fillMonth(0, 1);

			function fillMonth(weekNo, date) {
				let lastWeek = false;
				month.push([]);
				for (let i = 0; i < 7; i++) {
					appDate = Object.create(AppDate);
					if ((weekNo == 0 && i < state.currentMonth.day1Index) || lastWeek) {
						month[weekNo].push({});
					} else if (date != numOfDays) {
						appDate.init({ date, month: state.currentMonth.index, year: state.currentMonth.year, dayIndex: i });
						month[weekNo].push(appDate);
						date++;
					} else {
						lastWeek = true;
						state.currentMonth.dayLastIndex = i;
						appDate.init({ date, month: state.currentMonth.index, year: state.currentMonth.year, dayIndex: i });
						month[weekNo].push(appDate);
						commit('setMonth', month);
						state.currentMonth.weeks = month;
					}
				}
				if (!lastWeek) {
					fillMonth(weekNo + 1, date);
				}
			}
		},
    getPrev({ commit, state, dispatch }) {
			let vm = this;
			if (state.currentMonth.index === 0) {
				state.currentMonth.index = 11;
				state.currentMonth.year = state.currentMonth.year - 1;
			} else {
				state.currentMonth.index = state.currentMonth.index - 1;
			}
			commit('checkFeb');
      dispatch('getMonth', { date: state.months[state.currentMonth.index].days, dateIndex: (state.currentMonth.day1Index + 6) % 7 });
		},
    getNext({ commit, state, dispatch }) {
			if (state.currentMonth.index === 11) {
				state.currentMonth.index = 0;
				state.currentMonth.year = state.currentMonth.year + 1;
			} else {
				state.currentMonth.index = state.currentMonth.index + 1;
			}
			commit('checkFeb');
			dispatch('getMonth', { date: 1, dateIndex: (state.currentMonth.dayLastIndex + 1) % 7 });
		},
    selectDate({ commit, state, dispatch }, date) {
			if (date.dateText) {
				if (!date.day) date.day = new Date(date.dateText).getDay();
				if (date.month != state.currentMonth.index || date.year != state.currentMonth.year || state.currentMonth.weeks.length < 1) {
					commit('setMonthVal', { key: 'index', val: date.month });
					commit('setMonthVal', { key: 'year', val: date.year });
					commit('checkFeb');
					dispatch('getMonth', { date: date.date, dateIndex: date.day });
				}
				state.selectedDate = state.calDates[state.selectedLocation['city_id']][date.dateText] || date;
				state.isDateSelected = true;
			}
    },
    loadLocation({ commit, state }, { location, locationImport }) {
      let dateToselect;
      commit('setSelectedLocation', location);
			if (!state.calDates[state.selectedLocation['city_id']]) {
				commit('setCalDates', locationImport);
			}
			if (state.selectedDate && state.selectedDate.date) {
        dateToselect = state.selectedDate;
			} else {
        dateToselect = state.today;
			}
      return dateToselect;
    },
    selectLocation({ state, dispatch }, { location, callback }) {
			if (!state.calDates[location['city_id']]) {
				import('../assets/cal-data/location-' + location['city_id'])
					.then(locationImport => {
            callback({ location, locationImport: locationImport.default });
					})
			} else {
        callback({ location });
			}
		},
  },
  modules: {
  }
})
