<template>
	<div class="container column h-100">
		<div class="title row y-center">
			<div class="padding8 clickable fw-bold" @click="$router.push('/books')">x</div>
			<h2 class="w-flex">{{ bookName }}</h2>
		</div>
		<embed :src="require('../assets/pdf/' + $route.params.code + '.pdf')" type="application/pdf" width="100%" height="100%" />
	</div>
</template>
<script>
import gurus from '../assets/js/parampara';

export default {
	name: 'ViewBook',
	data() {
		return {
			bookName: ''
		}
	},
	created() {
		var vm = this;
		for (let i = 0; i < gurus.length; i++) {
			if (gurus[i].books) {
				for (let j = 0; j < gurus[i].books.length; j++) {
					const book = gurus[i].books[j];
					if (book.code === vm.$route.params.code) {
						vm.bookName = book.name;
					}
				}
			}
		}
	} 
}
</script>
<style lang="scss" scoped>
h2 {
	margin: 0;
	padding: 0 8px;
}

@media screen and (max-width: 959px) {
	.container {
		height: calc(100vh - 30px);
	}

	.title {
		padding: 10px 0 10px 70px;
		margin-bottom: 10px;
	}
}
</style>