<template>
	<div class="main">
		<div class="header">
			<div class="monthSelect row flex-md flex-lg" v-if="selectedLocation.name !== 'Select Location...'">
				<div class="row main-center cross-center clickable flex-md flex-lg" @click="$store.dispatch('getPrev')">&lt;</div>
				<div class="row main-center cross-center clickable flex">{{ months[currentMonth.index].name }} {{ currentMonth.year }}</div>
				<div class="row main-center cross-center clickable flex-md flex-lg" @click="$store.dispatch('getNext')">&gt;</div>
			</div>
			<CalLocationSelect />
		</div>
		<div class="container" v-if="selectedLocation.name !== 'Select Location...'">
			<CalMonth />
			<CalEventList v-if="selectedDate.dateText" />
		</div>
	</div>
</template>
<script>
import CalEventList from '../components/CalEventList.vue';
import CalLocationSelect from '../components/CalLocationSelect.vue';
import CalMonth from '../components/CalMonth.vue';

export default {
	name: 'Calendar',
	components: {
		CalEventList,
		CalLocationSelect,
		CalMonth
	},
	computed: {
		selectedLocation() {
			return this.$store.state.selectedLocation;
		},
		selectedDate() {
			return this.$store.state.selectedDate;
		},
		months() {
			return this.$store.state.months;
		},
		currentMonth() {
			return this.$store.state.currentMonth;
		}
	},
	created() {
		let vm = this;
		vm.$store.commit('setToday');
	}
}
</script>
