<template>
  <div class="col list" id="list">
    <div v-if="!$route.params.code" class="col flex main-center">
      <div class="row">
        <Person :person="person" :clickable="true" v-for="(person, personIndex) in list" :key="personIndex" />
      </div>
      <div v-if="$route.params.category === 'parampara'" class="info">In the year 2009, Srila Bhakti Sundar Govinda Maharaj appointed <router-link class="clickable padding4" to="acharyaboard/AcharyaMhj"><b>Srila Acharya Maharaj</b></router-link> as His Successor and President Acharya, and established <router-link class="clickable padding4" to="acharyaboard"><b>The Acharya Board</b></router-link>.</div>
    </div>
    <div v-else>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import _ from 'lodash';
import Person from '../components/Person.vue';

export default {
  name: 'Personalities',
  components: { Person },
  data() {
    return {
			list: []
    }
  },
  created() {
    var vm = this;
    vm.initList(vm.$route.params.category);
  },
  mounted() {
    const el = document.getElementById('list');
    el.onwheel = (event) => {
      el.scrollLeft += event.deltaY;
   }
  },
  methods: {
    initList(category) {
      var vm = this;
      vm.list = [];
      import('../assets/js/' + category)
        .then(dataImport => {
          vm.list = _.cloneDeep(dataImport.default);
        })
    }
  },
  watch: {
    $route(to) {
      this.initList(to.params.category);
    }
  }
}
</script>
<style lang="scss" scoped>
.list {
  @media screen and (min-width: 0px) and (max-width: 959px) {
    height: calc(100vh - 100px);
    max-width: 100vw;
    overflow-x: auto;
  }
}

.info {
  text-align: center;
  padding: 20px 50px 0;
}
</style>