<template>
  <div class="locationSelect">
    <div class="row cross-center clickable h-100" @click="showLocationList = !showLocationList">
      <div class="flex">{{ selectedLocation.name }}</div>
      <div class="w-10"></div>
    </div>
    <div class="locationList" v-if="showLocationList">
      <div v-for="location in locationList" :key="location['city_id']" class="clickable" @click="$emit('selectLocation', location); showLocationList = false;">{{ location.name }}</div>
    </div>
  </div>
</template>
<script>
import cities from '../assets/js/cities';

export default {
  name: 'CalLocationSelect',
	computed: {
		selectedLocation() {
			return this.$store.state.selectedLocation;
		}
	},
  data() {
    return {
      showLocationList: false,
			locationList: cities
    }
  },
	created() {
		var vm = this;
		const location = localStorage.location;
		const locationInt = parseInt(location);
		const attr = locationInt ? 'city_id' : 'name';
		const loadLocation = (locationObj) => {
			vm.$store.dispatch('loadLocation', locationObj)
				.then(dateToselect => {
					vm.$store.dispatch('selectDate', dateToselect);
				});
		};

		for (let i = 0; i < cities.length; i++) {
			if(cities[i][attr] == location) {
				vm.$store.dispatch('selectLocation', { location: cities[i], callback: loadLocation });
				break;
			}
		}
	}
}
</script>
<style lang="scss" scoped>
.locationSelect {
	border-radius: 3px;
	margin-bottom: 20px;
	box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.3);
	height: 50px;
}

.locationSelect {
	& div:nth-of-type(1) {
		padding-left: 10px;
	}

	& div:nth-child(1) div:nth-child(2) {
		font-size: 25px;
	}
}

.locationList {
	position: absolute;
	background-color: white;
	box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.3);
	min-width: 200px;
	overflow-y: auto;
	z-index: 2;
	max-height: 500px;

	&>div {
		padding: 10px;
	}
}

@media screen and (min-width: 768px) {
	.locationList,
	.locationSelect {
		width: 35%;
	}

	.locationSelect {
		min-width: 200px;
		margin-left: 10px;
	}
}

@media screen and (max-width: 767px) {
	.locationList {
		width: calc(100% - 16px);
	}
}
</style>