<template>
  <div>
    <article v-if="person.name">
      <h2>{{ person.name }}</h2>
      <div class="row main-center">
        <div class="img" :style="{ 'background-image': 'url(' + require('../assets/img/' + person.code + '.jpg') + ')' }"></div>
      </div>
      <div v-html="person.desc" class="p"></div>
    </article>
  </div>
</template>
<script>
import Person from '../components/Person.vue';

export default {
  name: 'Personality',
  components: {
    Person
  },
  data() {
    return {
      person: {}
    }
  },
  created() {
    var vm = this;
    import('../assets/js/' + vm.$route.params.category)
      .then((data) => {
        const personalities = data.default;
        for (let i = 0; i < personalities.length; i++) {
          if (personalities[i].code === vm.$route.params.code) vm.person = personalities[i];
        }
      })
  }
}
</script>
